"use client";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { Bell, CheckCircle, AlertTriangle, Info, Clock, Check, ChevronRight, ArrowLeft, Trash2 } from "lucide-react";

export default function VendorNotifications() {
  const [notifications, setNotifications] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedNote, setSelectedNote] = useState<any | null>(null);

  useEffect(() => {
    fetchNotifications();
  }, []);

  async function fetchNotifications() {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) return;

    const { data } = await supabase
      .from('notifications')
      .select('*')
      .eq('user_id', user.id)
      .order('created_at', { ascending: false });
      
    if (data) setNotifications(data);
    setLoading(false);
  }

  async function openNotification(note: any) {
    setSelectedNote(note);
    if (!note.is_read) {
      setNotifications(prev => prev.map(n => n.id === note.id ? {...n, is_read: true} : n));
      await supabase.from('notifications').update({ is_read: true }).eq('id', note.id);
    }
  }

  return (
    <div className="max-w-2xl mx-auto pb-24 px-4">
      {/* HEADER SECTION */}
      {!selectedNote && (
        <div className="flex items-center justify-between py-6">
          <div>
            <h1 className="text-3xl font-black text-gray-900 tracking-tighter uppercase italic leading-none">Inbox</h1>
            <p className="text-[10px] font-black text-gray-400 uppercase tracking-[0.2em] mt-1">Official Updates</p>
          </div>
          
          <div className="bg-emerald-50 px-4 py-2 rounded-2xl border border-emerald-100 flex items-center gap-2">
             <div className={`w-2 h-2 rounded-full bg-emerald-500 ${notifications.some(n => !n.is_read) ? 'animate-ping' : ''}`} />
             <span className="text-[10px] font-black text-emerald-700 uppercase tracking-widest">
               {notifications.filter(n => !n.is_read).length} New
             </span>
          </div>
        </div>
      )}

      {/* INBOX LIST VIEW */}
      {!selectedNote ? (
        <div className="space-y-3">
          {notifications.length === 0 && !loading && (
              <div className="text-center py-20 bg-gray-50 rounded-[2.5rem] border-2 border-dashed border-gray-200">
                  <Bell size={40} className="mx-auto text-gray-200 mb-4" />
                  <p className="text-gray-400 font-black uppercase text-[10px] tracking-widest">Your warehouse is quiet</p>
              </div>
          )}

          {notifications.map((note) => (
            <div 
              key={note.id} 
              onClick={() => openNotification(note)}
              className={`relative p-4 rounded-3xl border transition-all active:scale-[0.97] flex items-center gap-4 ${
                note.is_read 
                  ? "bg-white border-gray-100 opacity-60 shadow-sm" 
                  : "bg-white border-gray-900 shadow-xl shadow-gray-200"
              }`}
            >
              {/* STATUS ICON */}
              <div className={`w-12 h-12 rounded-2xl flex items-center justify-center shrink-0 ${
                note.type === 'warning' ? 'bg-amber-100 text-amber-600' :
                note.type === 'success' ? 'bg-emerald-100 text-emerald-600' :
                'bg-gray-100 text-gray-900'
              }`}>
                {note.type === 'warning' ? <AlertTriangle size={20}/> :
                 note.type === 'success' ? <CheckCircle size={20}/> :
                 <Info size={20}/>}
              </div>

              {/* TEXT CONTENT */}
              <div className="flex-1 min-w-0">
                <div className="flex justify-between items-start mb-0.5">
                   <h3 className={`font-black text-xs uppercase tracking-tight truncate pr-4 ${note.is_read ? 'text-gray-500' : 'text-gray-900'}`}>
                      {note.title}
                   </h3>
                   <span className="text-[8px] font-black text-gray-400 uppercase tracking-tighter whitespace-nowrap">
                      {new Date(note.created_at).toLocaleDateString([], { month: 'short', day: 'numeric' })}
                   </span>
                </div>
                
                {/* PREVIEW LINE */}
                <p className="text-[11px] text-gray-400 font-bold truncate">
                  {note.body || note.message}
                </p>
              </div>

              {!note.is_read && (
                 <div className="w-2 h-2 bg-emerald-500 rounded-full absolute -top-1 -right-1 border-2 border-white shadow-sm" />
              )}
            </div>
          ))}
        </div>
      ) : (
        /* PROFESSIONAL DETAIL VIEW */
        <div className="animate-in slide-in-from-bottom-10 duration-500">
            <button 
              onClick={() => setSelectedNote(null)}
              className="mb-6 flex items-center gap-2 text-gray-900 font-black text-[10px] uppercase tracking-widest bg-white border border-gray-200 py-3 px-6 rounded-full shadow-sm active:scale-95 transition"
            >
                <ArrowLeft size={14} /> Go Back to Inbox
            </button>

            <div className="bg-white rounded-[2.5rem] border border-gray-900 shadow-2xl shadow-gray-300 overflow-hidden">
                <div className="p-8">
                    <div className="flex items-center gap-4 mb-8">
                        <div className={`p-4 rounded-[1.2rem] ${
                            selectedNote.type === 'warning' ? 'bg-amber-100 text-amber-600' :
                            selectedNote.type === 'success' ? 'bg-emerald-100 text-emerald-600' :
                            'bg-gray-900 text-white'
                        }`}>
                            {selectedNote.type === 'warning' ? <AlertTriangle size={28}/> :
                             selectedNote.type === 'success' ? <CheckCircle size={28}/> :
                             <Info size={28}/>}
                        </div>
                        <div>
                            <h2 className="text-2xl font-black text-gray-900 leading-[0.9] uppercase tracking-tighter italic">
                                {selectedNote.title}
                            </h2>
                            <p className="text-[9px] font-black text-gray-400 uppercase tracking-widest mt-2 flex items-center gap-2">
                                <Clock size={10} /> Received: {new Date(selectedNote.created_at).toLocaleString()}
                            </p>
                        </div>
                    </div>

                    {/* CONTENT AREA */}
                    <div className="bg-gray-50 p-6 rounded-[2rem] border border-gray-100 min-h-[200px]">
                        <p className="text-gray-800 text-xs sm:text-sm leading-relaxed font-bold whitespace-pre-line">
                            {selectedNote.body || selectedNote.message}
                        </p>
                    </div>

                    <div className="mt-8 pt-6 border-t border-gray-100 flex flex-col sm:flex-row gap-4 items-center justify-between">
                        <div className="flex items-center gap-2 text-[9px] font-black text-emerald-600 uppercase tracking-widest">
                            <CheckCircle size={14} /> Official StoreLink Communication
                        </div>
                        <button 
                          onClick={() => setSelectedNote(null)}
                          className="w-full sm:w-auto px-8 py-4 bg-gray-900 text-white rounded-2xl font-black text-[10px] uppercase tracking-[0.2em] shadow-lg active:scale-95 transition"
                        >
                            Mark as Done
                        </button>
                    </div>
                </div>
            </div>
        </div>
      )}
    </div>
  );
}